import { Component } from "../core/common";
import movieStore, { searchMovies } from "../store/movie";

export default class MoiveListMore extends Component {
  constructor() {
    super({
      tagName: "button",
    });
    //? MovieListMore Ïª¥Ìè¨ÎÑåÌä∏ ÌôúÏÑ±Ìôî Ï°∞Í±¥
    //* 1. store.stateÏùò page valueÏôÄ pageMax valueÎ•º ÎπÑÍµê
    //* 2. pageMax value > page value
    //*    ? this.el.classList.remove('hide)
    //*    : this.el.classList.add('hide)
    //! ÌïÑÏàòÏ°∞Í±¥: pageMaxÏùò Î≥ÄÌôîÎ•º Ïã§ÏãúÍ∞ÑÏúºÎ°ú Íµ¨ÎèÖÌï¥ÏïºÌï®
    movieStore.subscribe("pageMax", () => {
      const { page, pageMax } = movieStore.state;
      pageMax > page
        ? this.el.classList.remove("hide")
        : this.el.classList.add("hide");
    });
  }

  render() {
    this.el.classList.add("btn", "view-more", "hide");
    this.el.textContent = "View more..";

    // ÏòÅÌôî Ï†ïÎ≥¥Î•º Ï∂îÍ∞ÄÎ°ú Í∞ÄÏ†∏Ïò§Îäî Í∏∞Îä•ÏùÑ ÏàòÌñâÌïòÎØÄÎ°ú, ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨Ìï¥ÏïºÌï®
    this.el.addEventListener("click", async () => {
      this.el.classList.add("hide");
      await searchMovies(movieStore.state.page + 1);
    });
  }
}
