import { Component } from "../core/common";
import movieStore, { searchMovies } from "../store/movie";
import MovieItem from "./MovieItem";
export default class MovieList extends Component {
  constructor() {
    super();
    movieStore.subscribe("movies", () => {
      // console.log("ÏòÅÌôî Ï†ïÎ≥¥ Í∞±Ïã†", movieStore.state.movies);
      this.render();
    });
    movieStore.subscribe("loading", () => {
      // console.log("Î°úÎî© Ï§ë..", movieStore.state.loading);
      this.render();
    });
    movieStore.subscribe("message", () => {
      // console.log("ÏòÅÌôî Í≤ÄÏÉâ Ï§ë..", movieStore.state.message);
      this.render();
    });
  }
  render() {
    this.el.classList.add("movie-list");
    this.el.innerHTML = /* html */ `
            ${
              movieStore.state.message
                ? `<div class="message">${movieStore.state.message}</div>`
                : '<div class="movies"></div>'
            }
            <div class="the-loader hide"></div> 
        `;

    /* Ï£ºÏùò : moviesEl ÏÉùÏÑ± Ï°∞Í±¥ÏùÄ movieStore.state.message Í∞Ä false¬†ÏùºÎïåÎßå ÏÉùÏÑ±ÎêúÎã§. */
    //* Ïù¥Îïå, .movide Í∞Ä this.elÏóê Ï∂îÍ∞ÄÎêòÏßÄ ÏïäÎäîÎã§Î©¥, moviesElÎäî Ï°∞ÌöåÌï† Ïàò ÏóÜÏúºÎØÄÎ°ú, null Í∞íÏùÑ Ìï†Îãπ Î∞õÍ≤å ÎêúÎã§.
    //* Îî∞ÎùºÏÑú, moviesElÍ∞Ä ÏûàÏùÑ ÎïåÎßå, append ÏãúÌÇ§Î©¥ ÎêòÎØÄÎ°ú, ÏÑ†ÌÉùÏ†Å Ïó∞ÏÇ∞ÏûêÎ•º Ï†ÅÏö©ÌïòÎ©¥ Ìï¥Í≤∞ Ìï† Ïàò ÏûàÎã§.
    const moviesEl = this.el.querySelector(".movies");
    moviesEl?.append(
      // Í∞ÅÍ∞ÅÏùò MovieItem Ïª¥Ìè¨ÎÑåÌä∏Îäî Î∞∞Ïó¥Î°ú moviesEl Ïóê Ï∂îÍ∞ÄÎêòÏßÄ ÏïäÎäîÎã§.
      // Î∞∞Ïó¥Ïù¥ ÏïÑÎãå ÌïòÎÇòÏùò ElÎ°úÏç® Ï∂îÍ∞ÄÎêòÏñ¥Ïïº ÌïòÎØÄÎ°ú,
      // mapÏùò Í≤∞Í≥ºÎ•º Ï†ÑÍ∞úÏó∞ÏÇ∞ÏûêÎ°ú append ÌïúÎã§.
      ...movieStore.state.movies.map((movie) => {
        return new MovieItem({ movie }).el;
      })
    );

    const loaderEl = this.el.querySelector(".the-loader");

    movieStore.state.loading
      ? loaderEl?.classList.remove("hide")
      : loaderEl?.classList.add("hide");
  }
}
